import { el, setChildren } from "redom";

/**    */
function createInputs() {
  const title = el(
    "h1.wrapper__input-descr",
    "   ?  :"
  );
  const textTimer = el(
    "p.text",
    " ,  - ",
    el("span.time", " 60")
  );

  const bodyInputFirst = el(".form-check.form-check-inline", [
    el(
      "label.form-check-label",
      { for: "first" },
      " ",
      el("input.js-input.first.form-check-input", {
        type: "radio",
        id: "first",
        value: "option1",
      })
    ),
  ]);
  const bodyInputSecond = el(".form-check.form-check-inline", [
    el(
      "label.form-check-label",
      { for: "second" },
      " ",
      el("input.js-input.form-check-input", {
        type: "radio",
        id: "second",
        value: "option2",
      })
    ),
  ]);
  const bodyInputThird = el(".form-check.form-check-inline", [
    el(
      "label.form-check-label",
      { for: "third" },
      " ",
      el("input.js-input.form-check-input", {
        type: "radio",
        id: "third",
        value: "option3",
      })
    ),
  ]);
  const bodyInputFourth = el(".form-check.form-check-inline", [
    el(
      "label.form-check-label",
      { for: "first" },
      " ",
      el("input.js-input.form-check-input", {
        type: "radio",
        id: "fourth",
        value: "option4",
      })
    ),
  ]);
  const form = el("form.form-input");
  setChildren(form, [
    bodyInputFirst,
    bodyInputSecond,
    bodyInputThird,
    bodyInputFourth,
  ]);

  const body = el(".wrapper__input");
  setChildren(body, [title, form, textTimer]);
  const container = el("div.container");
  setChildren(container, [body]);
  return container;
}

document.querySelector(".main").append(createInputs());

let container = document.getElementById("game");
let btnOfChoice = document.querySelectorAll(".js-input");
let timer = document.querySelector(".time");

let openCard = false;
let boardLocked = false;
let firstCard, secondCard, interval;

const openCardArr = [];

// 
const createCard = (id) => {
  const scene = document.createElement("div");
  const card = document.createElement("div");
  const frontCard = document.createElement("div");
  const backCard = document.createElement("div");

  scene.classList.add("scene");
  card.classList.add("card");
  card.setAttribute("data-id", id);
  frontCard.classList.add("card__face", "card-closed");
  backCard.classList.add("card__face", "card-open");
  backCard.textContent = id;
  backCard.id = id;

  card.append(frontCard);
  card.append(backCard);
  scene.append(card);
  container.append(scene);

  return {
    scene,
    card,
    frontCard,
    backCard,
  };
};

//  data-id    
const cardId = (numberOfcard, arr) => {
  for (let j = 0; j < numberOfcard / 2; j++) {
    arr.push(j);
    arr.push(j);
  }
};

//  
const shuffleCards = (array) => {
  let i = 0;
  let j = 0;
  let temp = null;
  for (i = array.length - 1; i > 0; i -= 1) {
    j = Math.floor(Math.random() * (i + 1));
    temp = array[i];
    array[i] = array[j];
    array[j] = temp;
  }
  return array;
};

/*    -   id  ,
           flipCard   
   .     */
const checkForMatch = () => {
  if (firstCard.dataset.id === secondCard.dataset.id) {
    firstCard.removeEventListener("click", flipCard);
    secondCard.removeEventListener("click", flipCard);
    openCardArr.push(firstCard, secondCard);
    finishedGame();
  } else {
    // ,           
    boardLocked = true;
    //          id
    setTimeout(() => {
      firstCard.classList.remove("open");
      secondCard.classList.remove("open");
      resetBoard();
    }, 500);
  }
};

//   
function flipCard() {
  if (boardLocked) return;

  if (this === firstCard) {
    //    
    return;
  }

  this.classList.add("open");

  //   -     
  //        
  if (!openCard) {
    openCard = true;
    firstCard = this;
  } else {
    openCard = false;
    secondCard = this;
    checkForMatch();
  }
}

//   -          
//     .      
const resetBoard = () => {
  openCard = boardLocked = false;
  firstCard = secondCard = null;
};

//  -      
//         
//      
//  60        
const setTimer = () => {
  btnOfChoice.forEach((btn) => {
    btn.addEventListener("click", () => {
      clearInterval(interval);
      timer.innerHTML = 60;
    });
  });

  interval = setInterval(() => {
    timer.innerHTML--;
    gameIsOver(interval);
  }, 1000);
};

//   
const clearGame = () => {
  container.innerHTML = "";
};

/*   -     , 
       ,   .
   true,    60( ),  .    
     */
const gameIsOver = () => {
  if (timer.innerHTML < 0) {
    timer.innerHTML = 0;
    const result = confirm(" ,   !");
    if (result) {
      timer.innerHTML = 60;
      clearGame();
    } else {
      timer.innerHTML = 60;
      clearGame();
    }
    clearInterval(interval);
  }
};

/*  .    -        
         ,    
  */
const finishedGame = () => {
  const cards = document.querySelectorAll(".card");
  setTimeout(() => {
    if (cards.length == openCardArr.length) {
      alert("   -    !");
      clearGame();
      timer.innerHTML = 60;
      clearInterval(interval);
    }
  }, 1000);
};

/* .    ,        
     ,       
      1   4   (  )
   ,          
         
   ,             
    ,   
          */

const createScene = () => {
  btnOfChoice.forEach((btn) => {
    btn.addEventListener("click", () => {
      let arr = [];
      let numberOfcard;
      if (btn.id == "first") {
        numberOfcard = 4;
        clearGame();
        setTimer(interval);
        cardId(numberOfcard, arr);

        arr = shuffleCards(arr);
        arr.forEach((id) => createCard(id));
      }
      if (btn.id == "second") {
        numberOfcard = 8;
        clearGame();
        setTimer(interval);
        cardId(numberOfcard, arr);

        arr = shuffleCards(arr);
        arr.forEach((id) => createCard(id));
      }
      if (btn.id == "third") {
        numberOfcard = 12;
        clearGame();
        setTimer(interval);
        cardId(numberOfcard, arr);

        arr = shuffleCards(arr);
        arr.forEach((id) => createCard(id));
      }
      if (btn.id == "fourth") {
        numberOfcard = 16;
        clearGame();
        setTimer(interval);
        cardId(numberOfcard, arr);

        arr = shuffleCards(arr);
        arr.forEach((id) => createCard(id));
      }

      const cards = document.querySelectorAll(".card");
      cards.forEach((card) => {
        card.addEventListener("click", flipCard);
      });
    });
  });
};
createScene();
